import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return{
            checked: [],
            name: '',
            introduce: '',
            days: '',
            price: '',
            value: [],
            list: [],
            plhdt: '建议售价0元',
            ServicePackId: '',
            user: this.$store.state.userInfo.Data,
        }
    },
    created(){
        this.pull()
    },
    beforeDestroy(){
        this.$store.dispatch('pushservice', null)
    },
    methods: {
        send(){
            const Items = []
            this.checked.forEach(v => {
                this.list.forEach(a => {
                    if(v === a.ServiceItemName){
                        Items.push({
                            ServiceItemId: a.ServiceItemId,
                            ServiceItemName: a.ServiceItemName,
                        })
                    }
                })
            })
            Items.forEach(v => {
                this.value.forEach(a => {
                    if(v.ServiceItemId === a.ServiceItemId){
                        v.NumValue = a.number
                        v.EachPrice = a.price
                    }
                })
            })
            // 如有ServicePackId为修改
            this.$post('/PlatFormAPI/ServicePackItem/SaveDrPackItem',this.$pick({
                ServicePackId: this.ServicePackId,
                DrId: this.user.User.DoctorId,
                DrName: this.user.User.DoctorName,
                ServicePackName: this.name,
                OriginalPrice: this.price,
                Price: this.price,
                OrgIds: this.user.User.OrgId,
                Description: this.introduce,
                ServiceLong: this.days,
                ServiceLongType: 1,
                Items
            })).then(o => {
                this.$toast('提交成功')
                this.$router.back()
            })
        },
        pull(){
            this.$post('/PlatFormAPI/ServicePackItem/QueryDrAndOrgItemByPage',{
                DrId: this.user.User.DoctorId,
                DrType: this.user.User.DrType
            }).then(o => {
                this.list = o.ReturnData
                o.ReturnData.forEach(v => {
                    this.value.push({
                        ServiceItemId: v.ServiceItemId,
                        price: '',
                        number: ''
                    })
                })
                //如果store有service，表示为修改
                const { service } = this.$store.state
                if(service){
                    this.ServicePackId = service.ServicePackId
                    service.Details.forEach(v => {
                        this.checked.push(v.ServiceItemName)
                        this.value.forEach((a,k) => {
                            if(a.ServiceItemId === v.ServiceItemId){
                                this.value[k] = {
                                    ServiceItemId: v.ServiceItemId,
                                    price: v.EachPrice,
                                    number: v.NumValue
                                }
                            }
                        })
                    })
                    this.name = service.ServicePackName
                    this.introduce = service.Description
                    this.days = service.ServiceLong
                    this.price = service.OriginalPrice
                }
            })
        },
        plhd(){
            let p = 0
            this.value.forEach(v => {
                p += v.price * v.number
                this.plhdt = `建议售价${ p }元`
            })
        }
    }
}



